@import 'reset-css';
@import '~@semi-bot/semi-theme-cgsa-alumni-applet/semi.css';

.hide{
    display: none;
}


.kanit{
    font-family: "kanit"
}


.alka {
    font-family: "alka"
}

.show-only-desktop {
    display: none;
}

.show-only-mobile {
    display: inherit;
}

#main-container {
    margin: 5px;
    min-height: 85vh;
}

.carousel-container {
    width: 100%;
    height: 300px;
}

.flex-center {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.flex-vertical-center {
    width: 100%;
    display: flex;
    align-items: center;
}

.color-primary {
    color: var(--semi-color-primary);
}

.bg-color-primary {
    background-color: var(--semi-color-primary);
}

@media (min-width: 768px) {
    .show-only-mobile {
        display: none;
    }

    .show-only-desktop {
        display: inherit;
    }

    #main-container {
        margin: 20px;
    }

    .carousel-container {
        width: 100%;
        height: 400px;
    }
}