body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: "kanit",monospace;
    src: url(./fonts/Kanit-Regular.ttf)format(ttf)
}

@font-face {
    font-family: "kanit",monospace;
    src: url(./fonts/Kanit-Bold.ttf)format(ttf);
    font-weight: bold
}

@font-face {
    font-family: "kanit",monospace;
    src: url(./fonts/Kanit-Italic.ttf)format(ttf);
    font-style: italic;
}

@font-face {
    font-family: "alka",monospace;
    src: local("alka"),url(./fonts/Alkalami-Regular.ttf)format(ttf);
    font-style: normal;
}
